.homeContainer {
    /* The image used */
    background-image: url(./mountain.jpg);

    /* Full height */

    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

body, html {
    height: 100%;
    margin: 0;
}


